import React, { Component } from 'react'
import { Card, CardTitle, CardText, CardActions, CardMenu, IconButton, Button } from 'react-mdl'
import '../CssFolder/Projects.css'

class ProjectCard extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
             im: this.props.imgUrl
        }
    }

    handleClcik(im){
        window.open(im,"tab")
    }
  
    render() {
        return (
            <Card className="single_card" shadow={5} style={{minWidth: '300px', borderRadius:'25px'}}>
                <CardTitle style={{color: '#fff', height: '176px',
                background: 'url('+this.state.im+') center/cover'}}>{this.props.title}</CardTitle>
                <CardText>{this.props.text}</CardText>

                <CardActions border>
                    <div style={{textAlign: 'end'}}>
                    <Button onClick={()=>{this.handleClcik(this.props.openUrl)}} className="view_btn" colored>View</Button>
                    </div>
                </CardActions>

                <CardMenu style={{color: '#fff'}}>
                    <IconButton name="share"></IconButton>
                </CardMenu>
            </Card>
        )
    }
}

export default ProjectCard
