import React, { Component } from 'react'
import { Grid, Cell } from 'react-mdl'
import '../CssFolder/LandingPageCss.css'
import imag from '../Images/pp.jpg'
import ToolsExpertness from './ToolsExpertness'

class LandingPage extends Component {
    render() {
        return (
            <div style={{width: '100%',  margin: 'auto'}}>
               <Grid className="grid_layout">
                   <Cell col={12}>
                       <img src={imag} alt="my_profile_pic" className="aavtarImg">
                       </img>

                       <div className="div1">
                           <h2 className="text1">Android Developer</h2>
                           <hr className="hr1"/>
                           <p className="txt2">Android Applications | Desktop Applications | Website Development</p>


                           <div className="Social_links">
                            
                            <a href="https://www.instagram.com/asawa.rohit/?hl=en" rel="noopener noreferrer" target="_blank">
                            <i className="fa fa-instagram" aria-hidden="true"/>
                            </a>

                            <a href="http://rohitasawa.c1.biz" rel="noopener noreferrer" target="_blank">
                            <i className="fa fa-globe" aria-hidden="true"/>
                            </a>
                        </div>

                       </div>

                        <div>
                            <p style={{color:'white', marginTop:'70px', fontSize:'30px', fontWeight:"bold",}}>My Tools Expertness</p>

                            <ToolsExpertness/>
                        </div>
                      
                   </Cell>
               </Grid>
               
            </div>
        )
    }
}

export default LandingPage
