import React, { Component } from 'react'
import { Card, CardTitle, CardText } from 'react-mdl'

class AboutMe_Card extends Component {
    render() {
        return (
            <div>
            <Card className="card1" shadow={5} style={{ width: '88%'}}>
                <CardTitle>{this.props.title}</CardTitle>
                <hr />
                <div>
                    <img className="small_icon" src={this.props.imgUrl} />
                </div>
        <CardText className="txt1 commonTxt">{this.props.text}</CardText>
            </Card>

        </div>
        )
    }
}

export default AboutMe_Card
