import React, { Component } from 'react'
import { Drawer, Content, Navigation, Layout, Header } from 'react-mdl';
import Main from './Main';
import {Link} from 'react-router-dom'
import '../CssFolder/NavBarCss.css'

class NavBar extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
             drawerStat: false
        }
    }


    hideToggle() {
        var selectorId = document.querySelector('.mdl-layout');
        selectorId.MaterialLayout.toggleDrawer();
    }


    render() {
        return (
            <div className="demo-big-content">
            <Layout fixedHeader >
                <Header transparent className="gradient" title="Rohit Asawa" >
                    <Navigation >
                        <Link to="/RohitPortfolio">Home</Link>
                        <Link to="/RohitPortfolio/projects">Projects</Link>
                    </Navigation>
                </Header>
                <Drawer title="Menu">
                    <Navigation >
                        <Link to="/RohitPortfolio" onClick={() => this.hideToggle()}>Home</Link>
                        <Link to="/RohitPortfolio/projects" onClick={() => this.hideToggle()}>Projects</Link>
                        <Link to="/RohitPortfolio/resume" onClick={() => this.hideToggle()}>My Resume</Link>
                        <Link to="/RohitPortfolio/aboutme" onClick={() => this.hideToggle()}>About Me</Link>
                        <Link to="/RohitPortfolio/contact" onClick={() => this.hideToggle()}>Contact</Link>
                    </Navigation>
                </Drawer>
                <Content>
                    <div className="page-content" />
                    <Main/>
                </Content>
            </Layout>

       
        </div>
        )
    }
}

export default NavBar
