import React, { Component } from 'react'
import '../CssFolder/AboutMe.css'
import imag from '../Images/pp.jpg'
import { Card, CardTitle, CardText } from 'react-mdl'
import AboutMe_Card from './AboutMe_Card'

class AboutMe extends Component {
    render() {
        return (
            <div className="Main">
                <div className="Txts">
                    <h2>Hello World!!!</h2>
                    <h4>I'm Rohit Asawa</h4>
                </div>
                <img className="myImage"
                    src={imag}
                />

                <div>
                    <p className="txt1">I am Android Developer and have deployed many realtime projects in real world. I also develop Websites and Desktop Applications (Windows). By getting experience from various internship program's I have developed various skill's in every field of Information Technology. I spend a lot of my time in learning and developing new skills which helps projects to be developed at great interface.</p>
                </div>

                <div>
                <p className="p1">My Offered Services</p>
                </div>

                <div >
                    <p className="p2">If you are an entrepreneur, you know that your success cannot depend on the opinions of others. Like the wind, opinions.</p>
                </div>

                <AboutMe_Card title="Android Development" imgUrl="http://businesstracker.in/RohitAsawa/static/media/social-media.png" text="Android app Development with awesome user interface and great funtionality. I can deliver highly optimized android applications that work seamlessly on all devices." />
                <AboutMe_Card title="Web Development" imgUrl="http://businesstracker.in/RohitAsawa/static/media/web.png" text="I build applications with maximum accuracy and security which run without any failure. Progressive web apps, Responsive web apps, Single page apps and many other applications at its extent." />
                <AboutMe_Card title="Desktop Applications" imgUrl="http://businesstracker.in/RohitAsawa/static/media/financing.png" text="I help you to build desired windows softwares at a reasonable cost that customers would love to use. Let's scale together." />
                <AboutMe_Card title="Graphic Design" imgUrl="http://businesstracker.in/RohitAsawa/static/media/interface.png" text="Looking for user-centric new age UI/UX, optimized for all platforms? I focus on identifying your target audience, simplifying navigation, minimizing user input and creating precise content." />

            </div>
        )
    }
}

export default AboutMe

