import React, { Component } from 'react'
import { Card, CardTitle, CardText, CardActions, CardMenu, IconButton, Button } from 'react-mdl'
import '../CssFolder/Projects.css'
import SimpleDialog from './SimpleDialog'
import copy from 'copy-to-clipboard'
import CopyDialog from './CopyDialog'

class ProjectCard extends Component {


    state = {
        isOpen: false,
        isCopy: false
    }

    constructor(props) {
        super(props)
    
        this.state = {
             im: this.props.imgUrl,
             copyTxt: this.props.openUrl
        
        }
    }
    
    handleClcik(im){
        if(im != "#")
        {
            window.open(im,"tab")
        }
        else{
           this.setState({
               isOpen: true
           })
            
        //    <SimpleDialog />
        }
        
    }

    Copytext(cop){
        this.setState({
            isCopy:true
        })
        copy(""+cop)
    }
  
    render() {
        return (
            <div >
            <Card className="single_card" shadow={5} style={{minWidth: '300px', borderRadius:'25px'}}>
                <CardTitle style={{color: '#fff', height: '176px',
                background: 'url('+this.state.im+') center/cover'}}>{this.props.title}</CardTitle>
                <CardText>{this.props.text}</CardText>

                <CardActions border>
                    <div style={{textAlign: 'end'}}>
                    <Button  onClick={()=>{this.handleClcik(this.props.openUrl)}} className="view_btn" colored>View</Button>
                    </div>
                </CardActions>


                <CardMenu style={{color: '#fff'}}>
                    <IconButton  onClick={()=>this.Copytext(this.props.openUrl)} name="share"></IconButton>
                </CardMenu>
            </Card>

        <SimpleDialog isOpen={this.state.isOpen}/>

        <CopyDialog isOpen={this.state.isCopy}/>
</div>
        )
    }
}

export default ProjectCard
