import React, { Component } from 'react'
import '../CssFolder/toos.css'
import { ProgressBar, Card } from 'react-mdl'

const desi = {boxShadow:'0px 0px 20px', backgroundColor:'white', width:'85%', textAlign:'clear', marginLeft:'auto', marginRight:'auto',
            paddingTop:'15px', borderRadius:'15px', paddingBottom:'8px'      , paddingLeft:'5px' , paddingRight:'5px'
};

class ToolsExpertness extends Component {

    render() {
        return (
            <div style={{ paddingTop: '20px', paddingRight:'10px', }}>

                <hr style={{marginTop:'-12px', borderWidth:'2px', borderColor:'white',paddingBottom:'10px'}}></hr>
               
                    <div className="cardI" style={desi}>
                        <progress style={{ textAlign: 'center', margin: 'auto' }} id="progressbar" value="98" max="100"></progress>
                        <p style={{ color: 'white', paddingTop: '15px', fontSize: '20px' }}>Android Studio 98%</p>
                    </div>
            

                <br />
                <div className="cardI" style={desi}>
                <progress style={{ textAlign: 'center', margin: 'auto' }} id="progressbar" value="95" max="100"></progress>
                <p style={{ color: 'white', paddingTop: '15px', fontSize: '20px' }}>NetBeans 95%</p>
                </div>

                <br />
                <div className="cardI" style={desi}>
                <progress style={{ textAlign: 'center', margin: 'auto' }} id="progressbar" value="94" max="100"></progress>
                <p style={{ color: 'white', paddingTop: '15px', fontSize: '20px' }}>Visual Studio Code 94%</p>
</div>

<br />
                <div className="cardI" style={desi}>
                <progress style={{ textAlign: 'center', margin: 'auto' }} id="progressbar" value="90" max="100"></progress>
                <p style={{ color: 'white', paddingTop: '15px', fontSize: '20px' }}>Github (Libraries Development) 90%</p>
</div>

                <br />
                <div className="cardI" style={desi}>
                <progress style={{ textAlign: 'center', margin: 'auto' }} id="progressbar" value="90" max="100"></progress>
                <p style={{ color: 'white', paddingTop: '15px', fontSize: '20px' }}>Photoshop 90%</p>
</div>

                <br />
                <div className="cardI" style={desi}>
                <progress style={{ textAlign: 'center', margin: 'auto' }} id="progressbar" value="85" max="100"></progress>
                <p style={{ color: 'white', paddingTop: '15px', fontSize: '20px' }}>Premiere Pro 85%</p>
</div>





            </div>
        )
    }
}

export default ToolsExpertness
