import React, { Component } from 'react'
import { Button, Dialog, DialogTitle, DialogContent, DialogActions } from 'react-mdl'

class SimpleDialog extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isOpen: true
        };
        this.handleOpenDialog = this.handleOpenDialog.bind(this);
        this.handleCloseDialog = this.handleCloseDialog.bind(this);

        

      }
     
    handleOpenDialog() {
        this.setState({
          isOpen: true
        });
      }
    
      handleCloseDialog() {
        this.setState({
          isOpen: false,
        });
      }

    render() {


        let dialog = (
            <div>
        
            <Dialog  style={{backgroundColor:'red', borderRadius:"25px", boxShadow:'0px 0px 20px'}} open={this.state.isOpen} onCancel={this.handleCloseDialog}>
              <DialogTitle  style={{color:'white'}}>Not available for now..</DialogTitle>
              <DialogContent>
                <p style={{color:'white'}}>please visit again later..this will get updated soon..</p>
              </DialogContent>
              <DialogActions>
            
                <Button  style={{color:'red', backgroundColor:'white'}} type='button' onClick={(this.handleCloseDialog)}>ok</Button>
              </DialogActions>
            </Dialog>
          </div>
          );

          if(!this.props.isOpen)
          {
              dialog =null;
          }
        return (

           
        <div>
            {dialog}
        </div>
        )
    }
}

export default SimpleDialog
