import React, { Component } from 'react'
import '../CssFolder/Contact.css'
import imag from '../Images/pp.jpg'

import { Grid, Cell, List, ListItem, ListItemContent } from 'react-mdl'

class Contact extends Component {
    render() {
        return (
            <div className="contact_div">
                <Grid className="contact_grid">
                    <Cell col={6}>
                        <div >
                            <h2>Rohit Asawa</h2>
                            <img height="200px"
                                src={imag} className="aavtarImg"
                            />

                            <div>
                                <p className="someText">I am Android Developer and have deployed many realtime projects in real world. I also develop Websites and Desktop Applications (Windows). By getting experience from various internship program's I have developed various skill's in every field of Information Technology.
I spend a lot of my time in learning and developing new skills which helps projects to be developed at great interface.</p>
                            </div>
                        </div>
                    </Cell>
                    <Cell col={6}>
                        <div>
                            <h2>Contact Me</h2>
                            <hr />

                            <div className="contact_list">
                                <List>
                                    <ListItem>
                                        <ListItemContent icon="phone"><p className="item1" >+91 7387191410</p></ListItemContent>
                                    </ListItem>
                                    <ListItem>
                                        <ListItemContent icon="mail"><p className="item1" >rohit.asawa21@gmail.com</p></ListItemContent>
                                    </ListItem>
                                    <ListItem>
                                        <ListItemContent icon="home"><p className="item1" >Pune, Maharastra</p></ListItemContent>
                                    </ListItem>
                                </List>

                            </div>

                        </div>
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Contact
