import React, { Component } from 'react'
import { Button, Dialog, DialogTitle, DialogContent, DialogActions } from 'react-mdl'

class CopyDialog extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: true
        };
        this.handleOpenDialog = this.handleOpenDialog.bind(this);
        this.handleCloseDialog = this.handleCloseDialog.bind(this);

        
      }
     
    handleOpenDialog() {
        this.setState({
          isOpen: true
        });
      }
    
      handleCloseDialog() {
        this.setState({
          isOpen: false,
        });
      }

    render() {


        let dialog = (
            <div>
        
            <Dialog  style={{backgroundColor:'rgb(115, 192, 0)', borderRadius:"25px", boxShadow:'0px 0px 20px'}} open={this.state.isOpen} onCancel={this.handleCloseDialog}>
              <DialogTitle  style={{color:'white'}}>Url copied.</DialogTitle>
              <DialogContent>
                <p style={{color:'white'}}>you can paste this url anywhere..</p>
              </DialogContent>
              <DialogActions>
            
                <Button  style={{color:'rgb(115, 192, 0)', backgroundColor:'white'}} type='button' onClick={(this.handleCloseDialog)}>ok</Button>
              </DialogActions>
            </Dialog>
          </div>
          );

          if(!this.props.isOpen)
          {
              dialog =null;
          }
        return (

           
        <div>
            {dialog}
        </div>
        )
    }
}

export default CopyDialog
