import React from 'react'
import LandingPage from './LandingPage'
import AboutMe from './AboutMe'
import Projects from './Projecrts'
import Resume from './Resume'
import Conatact from './Contact'
import { Switch, Route} from 'react-router-dom'

const Main = () =>(
    
    <Switch>
        <Route path="/RohitPortfolio" component={LandingPage}/>
        <Route path="/RohitPortfolio/aboutme" component={AboutMe}/>
        <Route path="/RohitPortfolio/projects" component={Projects}/>
        <Route path="/RohitPortfolio/resume" component={Resume}/>
        <Route path="/RohitPortfolio/contact" component={Conatact}/>
    </Switch>
)

export default Main;